import { NgAMap } from '../amap'
import { GoogleMap } from '../google'
import { flow, Event } from 'power-helper'
import { LatLng, MarkerAttr } from '../types'

type Channels = {
    click: any
}

export class MapMarker extends Event<Channels> {
    id?: string
    icon?: string
    aMap?: NgAMap
    aMapMarker?: AMap.Marker
    googleMap?: GoogleMap
    googleMarker?: google.maps.Marker
    googleInfowindow?: google.maps.InfoWindow
    constructor(map: GoogleMap | NgAMap, params: MarkerAttr) {
        super()
        this.id = params.id || flow.createUuid()
        this.icon = params.icon
        //  google map
        if (map instanceof GoogleMap) {
            this.googleMap = map
            this.googleInfowindow = new google.maps.InfoWindow({
                content: params.content
            })
            this.googleMarker = new google.maps.Marker({
                map: this.googleMap.map,
                icon: this.icon,
                animation: google.maps.Animation.DROP,
                position: params.position,
            })
            this.googleMarker.addListener('click', () => {
                this.emit('click', this.getPosition())
            })
            if (params.content) {
                this.googleInfowindow?.open({
                    map: this.googleMap?.map,
                    anchor: this.googleMarker
                })
            }
        }
        // 
        if (map instanceof NgAMap) {
            this.aMap = map
            if (this.aMap.map) {
                this.aMapMarker = new AMap.Marker({
                    icon: this.icon || 'https://a.amap.com/jsapi_demos/static/demo-center/icons/poi-marker-default.png',
                    anchor: 'bottom-center',
                    position: new AMap.LngLat(params.position.lng, params.position.lat)
                })
                if (params.content) {
                    this.aMapMarker.setLabel({
                        content: params.content,
                        offset: new AMap.Pixel(0, 42),
                        direction: 'center'
                    })
                }
                this.aMapMarker.setMap(this.aMap.map)
                this.aMapMarker.on('click', () => {
                    this.emit('click', this.getPosition())
                })
            }
        }
        params.onload?.(this)
    }

    getInMapMarkers() {
        if (this.googleMap) {
            return this.googleMap.markers.slice()
        }
        if (this.aMap) {
            return this.aMap.markers.slice()
        }
        return []
    }

    hideLabel() {
        if (this.googleInfowindow) {
            this.googleInfowindow.close()
        }
        if (this.aMapMarker) {
            this.aMapMarker.setLabel({
                content: '',
                offset: new AMap.Pixel(0, 0),
                direction: 'center'
            })
        }
    }

    hasLabel() {
        if (this.googleInfowindow) {
            // eslint-disable-next-line @typescript-eslint/ban-ts-comment
            // @ts-ignore
            return !!this.googleInfowindow.getAnchor()
        }
        if (this.aMapMarker) {
            return !!this.aMapMarker.getLabel().content
        }
        return false
    }

    setLabel(label: string) {
        if (this.googleMarker) {
            this.googleInfowindow?.open(this.googleMap?.map, this.googleMarker)
            this.googleInfowindow?.setContent(label)
        }
        if (this.aMapMarker) {
            this.aMapMarker.setLabel({
                content: label,
                offset: new AMap.Pixel(0, 42),
                direction: 'center'
            })
        }
    }

    remove() {
        if (this.googleMarker) {
            this.googleMarker.setMap(null)
            if (this.googleMap) {
                this.googleMap.markers = this.googleMap.markers.filter(marker => marker.id !== this.id)
            }
        }
        if (this.aMapMarker) {
            this.aMapMarker.setMap(null)
            if (this.aMap) {
                this.aMap.markers = this.aMap.markers.filter(marker => marker.id !== this.id)
            }
        }
    }

    moveTo(position: LatLng) {
        if (this.googleMarker) {
            this.googleMarker.setPosition(position)
        }
        if (this.aMapMarker) {
            this.aMapMarker.setPosition([
                position.lng,
                position.lat
            ])
            
        }
    }

    getPosition() {
        const output = {
            lat: 0,
            lng: 0
        }
        if (this.googleMarker) {
            let position = this.googleMarker.getPosition()
            if (position) {
                output.lat = position.lat()
                output.lng = position.lng()
            }
        }
        if (this.aMapMarker) {
            let position = this.aMapMarker.getPosition()
            if (position) {
                output.lat = position.getLat()
                output.lng = position.getLng()
            }
        }
        return output
    }
}
